import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import RoundInfo from "../components/RoundInfo";
import {Button} from "@mui/material";
import axios from "axios";

//const BASE_URL = "http://localhost:8080";
const BASE_URL = "//3.36.163.39:8080";

const Home = ({userObj}) => {

    const [rounds, setRounds] = useState([]);

    useEffect(() => {
        // TODO :    
        axios.get(`${BASE_URL}/round?userId=${userObj.userId}`)
            .then(response => setRounds(response.data))
            .catch(error => console.log(error));

    }, []);

    return (
        <div>
            <p> {userObj.nickname}   </p>
            {/* TODO :    +  */}
            {rounds ? (<RoundInfo rounds={rounds}/>) :
                (<p>  .</p>)
            }
            <Link to={process.env.PUBLIC_URL + "/roundInput"}>  </Link>
        </div>
    );
}

export default Home;